import React, { useState, useEffect, useRef } from 'react';
import DropDown from '../forms/DropDown.js';
import AddProjectInterface from './AddProjectInterface';
//import TimerComponent from '../hooks/TimerComponent';
import TrackTasks from './TrackTasks.js';
import TrackEvents from './TrackEvents.js';
import TrackCharge from './TrackCharge.js';
import TrackWaves from './TrackWaves.js';
import TrackJournal from './TrackJournal.js';
import LinkSaver from './LinkSaver.js';
import Sounds from './Sounds.js';
import { currentTime, currentDate } from './CurrentCalendar.js';
import initSession from './initSession';
import initProjects from './initProjects';
import initWaves from './initWaves';
import initTasks from './initTasks';
import initCharges from './initCharges';
import initEvents from './initEvents';
import trackables from './trackables';
import initLinkTracking from './initLinkTracking';
import initJournalTracking from './initJournalTracking';

const Tracker = () => {

    const [projects, setProjects] = useState(JSON.parse(localStorage.getItem('projects')) || initProjects);
    const [events, setEvents] = useState(JSON.parse(localStorage.getItem('eventTracking')) || initEvents);
    const [waves, setWaves] = useState(JSON.parse(localStorage.getItem('waveTracking')) || initWaves);
    const [links, setLinks] = useState(JSON.parse(localStorage.getItem('linkTracking')) || initLinkTracking);
    const [journals, setJournals] = useState(JSON.parse(localStorage.getItem('journalTracking')) || initJournalTracking);
    const [tasks, setTasks] = useState(JSON.parse(localStorage.getItem('taskTracking')) || initTasks);
    const [charges, setCharges] = useState(JSON.parse(localStorage.getItem('chargeTracking')) || initCharges);
    //const [scroll, setScroll] = useState();

    const [tracking, setTracking] = useState(localStorage.getItem('tracking'));

    const [isCollapsed, setIsCollapsed] = useState(false);
    const [todos, setCurrentTodos] = useState([]);
    const [newProjectDescription, setNewProjectDescription] = useState();
    //const [newTaskDescription, setNewTaskDescription] = useState('');
    //const [activeProjectIndex, setActiveProjectIndex] = useState(-1);
    const [elapsedTime, setElapsedTime] = useState(0);
    const [clock, setClock] = useState(0);
    const [description, setDescription] = useState();
    const [activated, setActivated] = useState(true);
    const [completed, setCompleted] = useState(false);

    const targetElementRef = useRef(null);

    const scrollToBottom = () => {
        //alert(`scrollToBottom`);
        if (targetElementRef.current) {
            //targetElementRef.current.scrollIntoView({ behavior: 'smooth', block: 'end' });
        }
    };

    const trackingMap = {
        'projects': [projects, setProjects],
        'tasks': [tasks, setTasks],
        'waves': [waves, setWaves],
        'events': [events, setEvents],
        'charges': [charges, setCharges],
        'links': [links, setLinks],
        'journals': [journals, setJournals],
    };
    const scrollIt = () => {
        window.scrollTo({
            top: document.body.scrollHeight,
            behavior: 'smooth'
        });
    }
    useEffect(() => {
        const storedEvents = localStorage.getItem('eventTracking');
        if (storedEvents) {
            setEvents(JSON.parse(storedEvents));
        } else {
            setEvents([initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0)]);
        }
        //console.log(`local Projects: ${JSON.stringify(localProjects(), null, 2)}`)
        if (projects === null) setProjects(initProjects);
        if (tasks === null) setTasks(initTasks);
        if (waves === null) setWaves(initWaves);
        if (events === null) setEvents(initEvents);
        if (charges === null) setCharges(initCharges);
        if (links === null) setLinks(initLinkTracking);
        if (journals === null) setJournals(initJournalTracking);
        console.log(`Tracker =>  journals: ${JSON.stringify(journals, null, 2)}`)
    }, []);

    useEffect(() => {
        localStorage.setItem('eventTracking', JSON.stringify(events));
    }, [events]);

    useEffect(() => {
        //scrollIt();
    }, [journals]);

    useEffect(() => {
        localStorage.setItem('tracking', tracking)
    }, [tracking]);

    const toggleTimer = () => {
        Sounds.playSound(0, 1);
        setActivated(!activated);
    }

    const toggleParentTimer = () => toggleTimer();

    const selectTracking = (event) => {
        setTracking(event.target.value);
    };


    const addProject = () => {
        const project = {
            description: newProjectDescription,
            createdDate: currentDate(),
            startTime: currentTime(),
            tasks: [],
            totalTime: 0,
            isCollapsed: false
        };
        const addLinkGroup = () => {
            const updatedLinks = [...links];
            const title = newProjectDescription;
            if (title) {
                const linkGroup = {
                    title: title,
                    links: [],
                    isCollapsed: false
                };
                updatedLinks.push(linkGroup)
                setLinks(updatedLinks);
            }
        };
        const addJournalGroup = () => {
            const updatedJournals = [...journals];
            const title = newProjectDescription;
            if (title) {
                const journalGroup = {
                    title: title,
                    journals: [],
                    isCollapsed: false
                };
                updatedJournals.push(journalGroup)
                setJournals(updatedJournals);
            }
        };
        /*
        if (tracking === 'project') 
            setProjects(prevProjects => [project, ...prevProjects]);
        if (tracking === 'events')
            setEvents(prevEvents => [project, ...prevEvents]);
        if (tracking === 'waves') 
            setProjects(prevProjects => [project, ...prevProjects]);
        */

        if ((tracking !== 'links' || tracking !== 'journals') && trackingMap.hasOwnProperty(tracking)) {
            trackingMap[tracking][1](prev => [project, ...prev]);
        }
        if (tracking === 'links') {
            addLinkGroup();
        }
        if (tracking === 'journal') {
            addJournalGroup();
        }
    };

    const deleteProject = (index) => {
        let updatedTrackingData = [...projects];
        if (trackingMap.hasOwnProperty(tracking)) {
            updatedTrackingData = [...trackingMap[tracking][0]];
            updatedTrackingData.splice(index, 1);
            trackingMap[tracking][1](updatedTrackingData);
        }
    };
    const deleteGroup = (index) => {
        let updatedTrackingData = [...projects];
        if (trackingMap.hasOwnProperty(tracking)) {
            updatedTrackingData = [...trackingMap[tracking][0]];
            updatedTrackingData.splice(index, 1);
            trackingMap[tracking][1](updatedTrackingData);
        }
    };

    const toggleCollapseAll = () => {
        setIsCollapsed(!isCollapsed);
    };
    const toggleCollapseSubmenu = (projectIndex) => {
        //setIsCollapsed(!isCollapsed);
        const updatedProjects = [...projects];
        const project = updatedProjects[projectIndex];
        project.isCollapsed = !project.isCollapsed;

        setProjects(updatedProjects);
        return (!project.isCollapsed) ? setIsCollapsed(false) : null;
    };

    const getProjectTime = (project) => {
        let projectTotal = 0;
        trackingMap[tracking][0].forEach((event) => {
            projectTotal += event.runningTime;
        });
        project.totalTime = projectTotal;
        return projectTotal;
    };

    const capitalizeString = (str) => str.charAt(0).toUpperCase() + str.slice(1);
    const getCategory = (category) => {
        let label = capitalizeString(category);
        if (label.endsWith('s')) {
            label = capitalizeString(label).slice(0, -1);
        }
        return label;
    }

    return <div className='mt--30 bg-lite'>
        <div className='containerBox'>
            <div className='flexContainer containerBox'>
                <div className='flex2Column columnRightAlign size25 r-5 bold color-soft'>
                    Tracking:
                </div>
                <div className='flex2Column columnLeftAlign'>
                    <DropDown
                        value={tracking}
                        options={trackables}
                        onChange={selectTracking}
                        classes='color-lite size20 bg-dark'
                    />
                </div>
            </div>
            {//(tracking === 'tasks' || tracking === 'events')
                (tracking !== '')
                    ? <AddProjectInterface
                        newProjectDescription={newProjectDescription}
                        setNewProjectDescription={setNewProjectDescription}
                        addProject={addProject}
                    />
                    : <React.Fragment></React.Fragment>
            }
        </div>

        <div className='containerBox'>
            <div className='containerBox size25 color-lite bg-lite bold button' onClick={toggleCollapseAll}>
                {isCollapsed ? 'Expand All' : 'Collapse All'}
            </div>
        </div>
        {(tracking === 'tasks')
            ? <TrackTasks
                tracking={tracking}
                tasks={tasks}
                setTasks={setTasks}
                getProjectTime={getProjectTime}
                deleteProject={deleteProject}
                toggleCollapseSubmenu={toggleCollapseSubmenu}
                isCollapsed={isCollapsed}
                toggleParentTimer={toggleParentTimer}
            />
            : <React.Fragment></React.Fragment>
        }
        {(tracking === 'projects')
            ? <TrackTasks
                tracking={tracking}
                tasks={projects}
                setTasks={setProjects}
                getProjectTime={getProjectTime}
                deleteProject={deleteProject}
                toggleCollapseSubmenu={toggleCollapseSubmenu}
                isCollapsed={isCollapsed}
                toggleParentTimer={toggleParentTimer}
            />
            : <React.Fragment></React.Fragment>
        }
        {(tracking === 'events')
            ? <TrackEvents
                events={events}
                setEvents={setEvents}
                getProjectTime={getProjectTime}
                deleteProject={deleteProject}
                toggleCollapseSubmenu={toggleCollapseSubmenu}
                isCollapsed={isCollapsed}
                toggleParentTimer={toggleParentTimer}
            />
            : <React.Fragment></React.Fragment>
        }

        {(tracking === 'charges') ?
            <TrackCharge
                charges={charges}
                setCharges={setCharges}
                newProjectDescription={newProjectDescription}
                setNewProjectDescription={setNewProjectDescription}
                getProjectTime={getProjectTime}
                toggleCollapseSubmenu={toggleCollapseSubmenu}
                isCollapsed={isCollapsed}
            />
            : <React.Fragment></React.Fragment>
        }

        {(tracking === 'waves') ?
            <TrackWaves
                waves={waves}
                setWaves={setWaves}
                getProjectTime={getProjectTime}
                deleteProject={deleteProject}
                isCollapsed={isCollapsed}
            />
            : <React.Fragment></React.Fragment>
        }
        {(tracking === 'links') ?
            <LinkSaver
                links={links}
                setLinks={setLinks}
                deleteGroup={deleteGroup}
                isCollapsed={isCollapsed}
            />
            : <React.Fragment></React.Fragment>
        }
        {(tracking === 'journal') ?
            <TrackJournal
                journals={journals}
                setJournals={setJournals}
                isCollapsed={isCollapsed}
                //setScroll={setScroll}
                targetElementRef={targetElementRef}
                scrollToBottom={scrollToBottom}
            />
            : <React.Fragment></React.Fragment>
        }
    </div>
};

export default Tracker
