import React, { useState, useEffect } from 'react';
import { currentTime, currentDate } from './CurrentCalendar';
import initSession from './initSession';
import initTask from './initTask';
import getTotalTime from './getTotalTime';
import icons from './icons';

const TrackCharge = ({
    charges,
    setCharges,
    newProjectDescription,
    setNewProjectDescription,
    getProjectTime,
    toggleCollapseSubmenu,
    isCollapsed
}) => {

    useEffect(() => {
        const storedCharges = localStorage.getItem('chargeTracking');
        if (storedCharges && (JSON.stringify(storedCharges) !== '[null]')) {
            setCharges(JSON.parse(storedCharges));
        } else {
            setCharges([initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0)]);                
        }
    }, []);

    useEffect(() => {
        localStorage.setItem('chargeTracking', JSON.stringify(charges));
    }, [charges]);

    const addCharge = (index) => {

        const batteryDescription = prompt('Battery Type:');
        const batteryLevel = prompt('Battery Level:');
        const chargeDescription = prompt('Solar Type:');
        const chargeLogDescription = `${batteryDescription} + ${chargeDescription}: ${batteryLevel}-100%`
        if (batteryDescription) {
            const sessions = [];
            sessions.push(initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0));
            const task = initTask(chargeLogDescription, sessions);
            const updatedCharges = [...charges];
            updatedCharges[index].tasks.unshift(task);
            updatedCharges[index].isCollapsed = false;
            setCharges(updatedCharges);
        }
    };
    const handleStartTask = (chargeProjectIndex, taskIndex) => {
        const updatedCharges = [...charges];
        const task = updatedCharges[chargeProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = true;
        //task.startDate = currentDate();
        task.sessions.push(initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0))
        setCharges(updatedCharges);
        startTimer(chargeProjectIndex, taskIndex);
    };
    function calculateTotalRunningTime(task) {
        let totalRunningTime = 0;
        task.sessions.forEach((session) => {
            totalRunningTime += session.runningTime;
        });
        return totalRunningTime;
    }
    const handleStopTask = (chargeProjectIndex, taskIndex) => {
        const updatedCharges = [...charges];
        const task = updatedCharges[chargeProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = false;
        const startTime = task.sessions[task.sessions.length - 1].startTime;
        const startDate = task.sessions[task.sessions.length - 1].startDate;
        const endTime = currentTime();
        const endDate = currentDate();
        const totalTimeMilliseconds = Math.round(endTime - startTime);
        const totalTime = getTotalTime(totalTimeMilliseconds);
        let runningTime = task.sessions[task.sessions.length - 1].runningTime;
        //alert(`runningTime1: ${runningTime} task.sessions.length: ${task.sessions.length}`)
        runningTime = (runningTime === undefined) ? task.sessions[task.sessions.length - 2].runningTime : runningTime;
        //alert(`runningTime2: ${runningTime}`)
        runningTime = (runningTime === '0s') ? totalTimeMilliseconds : (runningTime + totalTimeMilliseconds);
        const runningTimeDisplay = getTotalTime(runningTime);
        const session = {
            startDate: startDate,
            startTime: startTime,
            subTasks: [{
                startDate: startDate,
                startTime: startTime,
                endDate: endDate,
                endTime: endTime,
            }],
            endDate: endDate,
            endTime: endTime,
            totalTime: totalTime,
            runningTime: runningTime,
            runningTimeDisplay: runningTimeDisplay
        }
        task.sessions.push(session);
        console.log(`handleStopTask => task:${JSON.stringify(task,null,2)}`)
        const taskRunningTime = calculateTotalRunningTime(task);
        const taskRunningTimeDisplay = getTotalTime(taskRunningTime);
        console.log(`handleStopTask => taskRunnningTime: ${taskRunningTime} taskRunningTimeDisplay: ${taskRunningTimeDisplay}`)
        task.runningTime = taskRunningTime;
        task.runningTimeDisplay = taskRunningTimeDisplay;
        task.sessions.map((session, index) => console.log(`handleStopTask => session(${index}): runningTime: ${session.runningTime} totalTime: ${session.totalTime}`));
        setCharges(updatedCharges);
        stopTimer(chargeProjectIndex, taskIndex);
    };
    const startTimer = (chargeProjectIndex, taskIndex) => {
        const updatedCharges = [...charges];
        const task = updatedCharges[chargeProjectIndex].tasks[taskIndex];

        task.timerId = setInterval(() => {
            setCharges(updatedCharges);
        }, 1000);
    };

    const stopTimer = (chargeProjectIndex, taskIndex) => {
        const updatedCharges = [...charges];
        const task = updatedCharges[chargeProjectIndex].tasks[taskIndex];
        const session = task.sessions[task.sessions.length - 1]
        clearInterval(task.timerId);
        task.sessions.splice((task.sessions.length - 2), 1);
        task.totalTime = task.totalTime + session.runningTime;
        setCharges(updatedCharges);
    };
    const handleDeleteTask = (chargeProjectIndex, taskIndex) => {
        const updatedCharges = [...charges];
        updatedCharges[chargeProjectIndex].tasks.splice(taskIndex, 1);
        setCharges(updatedCharges);
    };
    const handleAddProject = () => {
        console.log(`handleAddProject => charges: ${JSON.stringify(charges, null, 2)}`);
        const chargeProject = {
            description: newProjectDescription,
            createdDate: currentDate(),
            startTime: currentTime(),
            tasks: []
        };
        if (charges !== null) {
            setCharges(prevCharges => [chargeProject, ...prevCharges]);
        } else {
            setCharges([chargeProject]);
        }
    };
    const handleDeleteCharges = (index) => {
        const updatedCharges = [...charges];
        updatedCharges.splice(index, 1);
        setCharges(updatedCharges);
    };
    const getTasks = (chargeProject, chargeProjectIndex) => {
        return (
            chargeProject.tasks.map((task, taskIndex) => (
                <div key={`task${taskIndex}`} className='containerBox lowerBorder'>
                    <div className='containerBox'>
                        <div className='bold color-yellow size25 lh-30'>{task.description}</div>
                        <div className='containerBox bold color-lite size25'>
                            <div className='mb-20'>{task.sessions.length} Total</div>
                            <div className='containerBox bg-veryLite'>{task.runningTimeDisplay}</div>
                        </div>
                    </div>
                    <div className='containerBox'>
                        {task.sessions.map((session, sessionIndex) => (
                            <div key={`session${sessionIndex}`} className='color-lite'>
                                <div className='containerBox'>
                                    <div className='columnLeftAlign size25 mb-5'>
                                        <span className='bold mr-10 color-yellow size25 lh-30'>
                                            {sessionIndex + 1}.
                                        </span>
                                        {String(session.startDate).split(', ')[0]}
                                    </div>
                                    <div className='ml-30 columnLeftAlign size15'>
                                        {String(session.startDate).split(', ')[1]} - {String(session.endDate).split(', ')[1]}
                                    </div>
                                    <div className='ml-30 columnLeftAlign size20 bold'>
                                        {session.totalTime}
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                    <div className='containerBox flexContainer'>
                        {task.isRunning ? (
                            <div className='containerBox flex2Column m-5 bg-lite button' onClick={() => handleStopTask(chargeProjectIndex, taskIndex)}>
                                {icons.stop} Stop
                            </div>
                        ) : (
                            <div className='containerBox flex2Column m-5  bg-lite button' onClick={() => handleStartTask(chargeProjectIndex, taskIndex)}>
                                {icons.track} Start
                            </div>
                        )}
                        <div className='containerBox flex2Column m-5  bg-lite button' onClick={() => handleDeleteTask(chargeProjectIndex, taskIndex)}>
                            {icons.delete} Delete
                        </div>
                    </div>
                </div>
            ))
        )
    }

    return <div>
        {
            <div className='containerBox'>
                <div className='containerBox bold size30 color-yellow'>Charge Log</div>
                <input
                    className='containerBox width-100-percent'
                    type="text"
                    value={newProjectDescription}
                    onChange={(e) => setNewProjectDescription(e.target.value)}
                    onKeyDown={(e) => {
                        if (e.key === 'Enter') {
                            e.preventDefault();
                            handleAddProject();
                        }
                    }}
                />

                <div className='containerBox bg-lite button bold' onClick={handleAddProject}>
                    Add Project
                </div>
            </div>
        }
        <div>
            {(charges !== null)
                ? charges.map((chargeProject, chargeProjectIndex) => (
                    <div key={`chargeProject${chargeProjectIndex}`} className='containerBox'>
                        <div className='containerBox'>
                            <div className='containerBox size30 mb-10 bold color-neogreen lh-30'>
                                {chargeProject.description}
                            </div>
                            <div className='containerBox'>
                                <div className='color-lite size25 bold m-5'>
                                    {String(chargeProject.createdDate).split(', ')[0]}
                                </div>
                                <div className='color-lite size25 m-5'>
                                    {String(chargeProject.createdDate).split(', ')[1]}
                                </div>
                                <div className='color-lite size20 m-10'>
                                    {icons.track} Start {getTotalTime(getProjectTime(chargeProject))}
                                </div>
                            </div>
                            <div className='containerBox flexContainer'>
                                <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => addCharge(chargeProjectIndex)}>
                                    + {icons.track} Start
                                </div>
                                <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => handleDeleteCharges(chargeProjectIndex)}>
                                    {icons.delete} Delete
                                </div>
                                <div className='containerBox flex3Column m-10 bg-lite button' onClick={() => toggleCollapseSubmenu(chargeProjectIndex)}>
                                    {chargeProject.isCollapsed ? 'Expand' : 'Collapse'}
                                </div>
                            </div>
                        </div>
                        <div>
                            {
                                (isCollapsed || chargeProject.isCollapsed)
                                    ? null
                                    : getTasks(chargeProject, chargeProjectIndex)
                            }
                        </div>
                    </div>
                ))
                : <div className='containerBox'>No Charges Recorded</div>
            }
        </div>
    </div>
}

export default TrackCharge