import React, { useState, useEffect } from 'react';
import Sounds from '../utils/Sounds';
import icons from '../utils/icons';

const NumberInputKeypad = ({
    onEnter,
    players,
    currentScoreIndex,
    playerId,
    score,
    displayNumberPad,
    setPlayers
}) => {
    const [displayValue, setDisplayValue] = useState('');
    /*
    const interferenceCount = () => {
        const regex = new RegExp(icons.dont, 'g');
        const matches = displayValue.match(regex);
        return matches ? matches.length : 0;
    }
    */
    const addInterference = () => {
        let interferenceCount = 1;
        if (players[playerId].interferenceCount) {
            interferenceCount = players[playerId].interferenceCount + 1;
        }
        //alert(`addInterference => interferenceCount: ${interferenceCount}`)
        return (interferenceCount < 4) ? interferenceCount : 3;
    }
    const removeInterference = () => {
        let interferenceCount = 0;
        if (players[playerId].interferenceCount) {
            interferenceCount = players[playerId].interferenceCount - 1;
        }
        //alert(`removeInterference => interferenceCount: ${interferenceCount}`);
        return (interferenceCount < 0) ? 0 : interferenceCount;
    }
    const isInterference = () =>  {
        const regex = new RegExp(icons.dont, 'g');
        const count = (displayValue.match(regex) || []).length;
        const filteredString = displayValue.replace(new RegExp(`[^${icons.dont}]`, 'g'), '');
        return [count, displayValue];

    }
    const displayInterenceCount = (value) => {
        const character = icons.dont;
        let interferenceCount = 0;
        let interferenceDisplay = '';
        if (value === `- ${icons.dont}`) {
            interferenceCount = (removeInterference() === 1) ? 0 : (removeInterference()-2);
            interferenceDisplay = character.repeat(interferenceCount);
        } else {
            interferenceDisplay = character.repeat(addInterference())
        }
        setDisplayValue(interferenceDisplay);
    }
    const getPlayerInterference = () => {
        let interferenceCount = 0;
        const newPlayers = [...players];
        if (displayValue === `NP ${icons.dont}`) {
            interferenceCount = 1;
        } else if (displayValue === `P ${icons.dont}`) {
            interferenceCount = 2;
        } else if (displayValue === `5Min ${icons.dont}`) {
            interferenceCount = 3;
        }
        if (players[playerId].interferenceCount) {
            interferenceCount = interferenceCount + Number(players[playerId].interferenceCount);
        }
        //alert(`getPlayerInterference => displayValue: ${displayValue} interferenceCount: ${interferenceCount}`)
        newPlayers[playerId].interferenceCount = (interferenceCount<4) ? interferenceCount : 3;
        return newPlayers;
    }
    const handleButtonClick = (value) => {
        console.log(`handleButtonClick => value: ${value} displayValue: |${displayValue}|`)
        if (value === 'Enter') {
            if (displayValue == '') {
                onEnter(score);
            } else if (isInterference()[0] > 0) {
                setPlayers(getPlayerInterference());
                onEnter(isInterference()[1]);
            } else if (displayValue != '') {
                onEnter(parseFloat(displayValue));
            } else {
                onEnter(parseFloat(score));
            }
            setDisplayValue('');
        } else if (value === `- ${icons.dont}`) {
            displayInterenceCount(value);
            setDisplayValue(value);
        } else if (value === `+ ${icons.dont}`) {
            displayInterenceCount(value);
        } else if (value === `NP ${icons.dont}`) { 
            setDisplayValue(value);
        } else if (value === `P ${icons.dont}`) { 
            setDisplayValue(value);
        } else if (value === `5Min ${icons.dont}`) { 
            setDisplayValue(value);
        } else if (value === 'Clear') {
            setDisplayValue('');
        } else if (value === icons.backspace) {
            setDisplayValue((prevValue) => prevValue.slice(0, -1));
        } else {
            setDisplayValue((prevValue) => prevValue + value);
        }
        Sounds.playSound(0, 1);
    };

    useEffect(() => {
        if (displayNumberPad) {

        }
    }, [displayNumberPad]);

    const buttonClasses = 'button fancyClick flex3Column bg-tintedMediumDark p-15 r-50 m-2 brdr-light';

    const renderButtonRow1 = () => {
        const buttons = ['1', '2', '3'];

        return (
            <div className='flexContainer'>
                {
                    buttons.map((button, index) => (
                        <div className={buttonClasses} key={index} onClick={() => handleButtonClick(button)}>
                            {button}
                        </div>
                    ))
                }
            </div>
        )
    };
    const renderButtonRow2 = () => {
        const buttons = ['4', '5', '6'];

        return (
            <div className='flexContainer'>
                {
                    buttons.map((button, index) => (
                        <div className={buttonClasses} key={index} onClick={() => handleButtonClick(button)}>
                            {button}
                        </div>
                    ))
                }
            </div>
        )
    };
    const renderButtonRow3 = () => {
        const buttons = ['7', '8', '9'];

        return (
            <div className='flexContainer'>
                {
                    buttons.map((button, index) => (
                        <div className={buttonClasses} key={index} onClick={() => handleButtonClick(button)}>
                            {button}
                        </div>
                    ))
                }
            </div>
        )
    };
    const renderButtonRow4 = () => {
        const buttons = ['.5', '0', '.'];

        return (
            <div className='flexContainer'>
                {
                    buttons.map((button, index) => (
                        <div className={buttonClasses} key={index} onClick={() => handleButtonClick(button)}>
                            {button}
                        </div>
                    ))
                }
            </div>
        )
    };

    const renderButtonRow5 = () => {
        const buttons = [
            `NP ${icons.dont}`, 
            `P ${icons.dont}`, 
            `5Min ${icons.dont}`
        ];

        return (
            <div className='flexContainer'>
                {
                    buttons.map((button, index) => (
                        <div className={buttonClasses} key={index} onClick={() => handleButtonClick(button)}>
                            {button}
                        </div>
                    ))
                }
            </div>
        )
    };
    const renderButtonRow6 = () => {
        const buttons = [icons.backspace, 'Enter', 'Clear'];

        return (
            <div className='flexContainer'>
                {
                    buttons.map((button, index) => (
                        <div className={buttonClasses} key={index} onClick={() => handleButtonClick(button)}>
                            {button}
                        </div>
                    ))
                }
            </div>
        )
    };
    const renderButtonRow7 = () => {
        const buttons = [icons.backspace, `+ ${icons.dont}`, 'Clear'];

        return (
            <div className='flexContainer'>
                {
                    buttons.map((button, index) => (
                        <div className={buttonClasses} key={index} onClick={() => handleButtonClick(button)}>
                            {button}
                        </div>
                    ))
                }
            </div>
        )
    };
    const renderButtonRow8 = () => {
        const buttons = ['Enter'];

        return (
            <div className='flexContainer'>
                {
                    buttons.map((button, index) => (
                        <div className={buttonClasses} key={index} onClick={() => handleButtonClick(button)}>
                            {button}
                        </div>
                    ))
                }
            </div>
        )
    };

    const prompt = () => `Enter ${players[playerId].name}'s wave ${currentScoreIndex + 1} score`;

    return (
        (displayNumberPad)
            ? <div className='containerBox bg-tintedMediumDark bold'>
                <div className='containerBox ht-60'>
                    <div className='fl-left minHeight40 pt-15'>
                        {prompt()}: 
                    </div>
                    <div className='containerBox bg-white color-dark ml-120 ht-auto minHeight40 minWidth50 fl-right'>
                        {displayValue}
                    </div>
                </div>
                <div className='containerBox'>
                    {renderButtonRow1()}
                    {renderButtonRow2()}
                    {renderButtonRow3()}
                    {renderButtonRow4()}
                    {renderButtonRow5()}
                    {renderButtonRow6()}
                </div>
            </div>
            : <div></div>
    );
};

export default NumberInputKeypad;