import React, { useState, useEffect } from 'react';
import thumbsUp from '../../assets/images/ThumbsUp.png';
import thumbsDown from '../../assets/images/ThumbsDown.png';
import {currentTime, currentDate} from '../utils/CurrentCalendar';
import initSession from './initSession';
import initWaves from './initWaves';
import icons from './icons';
import getTotalTime from './getTotalTime';

const TrackWaves = ({
    waves,
    setWaves,
    getProjectTime,
    deleteProject,
    isCollapsed
}) => {
    
    useEffect(() => {
        localStorage.setItem('waveTracking', JSON.stringify(waves));
    }, [waves]);

    useEffect(() => {
        const storedWaves = localStorage.getItem('waveTracking');
        if (storedWaves) {
            setWaves(JSON.parse(storedWaves));
        } else {
            setWaves([initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0)]);
        }
    }, []);

    const likeWave = (waveProjectIndex, taskIndex, sessionIndex) => {
        const newWaves = [...waves];
        const updatedSession = newWaves[waveProjectIndex].tasks[taskIndex].sessions[sessionIndex];
        newWaves[waveProjectIndex].tasks[taskIndex].sessions[sessionIndex].isLiked = !updatedSession.isLiked;
        setWaves(newWaves);
    }

    const addSet = (waveProjectIndex) => {
        const updatedWaves = [...waves];
        const taskDescription = `Set ${updatedWaves[waveProjectIndex].tasks.length + 1}`;
        if (taskDescription) {
            const sessions = [];
            sessions.push(initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0));
            const task = {
                startTime: currentTime(),
                endTime: '',
                description: taskDescription,
                sessions: sessions,
                isRunning: true,
                runningTime: '0s'
            };
            updatedWaves[waveProjectIndex].tasks.unshift(task);
            updatedWaves[waveProjectIndex].isCollapsed = false;
            setWaves(updatedWaves);
        }
    };

    const handleDeleteTask = (waveProjectIndex, taskIndex) => {
        const updatedWaves = [...waves];
        updatedWaves[waveProjectIndex].tasks.splice(taskIndex, 1);
        setWaves(updatedWaves);
    };

    const addWave = (waveProjectIndex, taskIndex) => {
        const updatedWaves = [...waves];
        const task = updatedWaves[waveProjectIndex].tasks[taskIndex];
        console.log(`updatedWaves[${waveProjectIndex}].tasks: ${JSON.stringify(updatedWaves[waveProjectIndex].tasks, null,2)}`)
        task.sessions = task.sessions || [];
        task.isRunning = false;
        //task.startDate = currentDate();
        task.sessions.push(initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0))
        setWaves(updatedWaves);
    };
    const subtractWave = (waveProjectIndex, taskIndex) => {
        const updatedWaves = [...waves];
        const task = updatedWaves[waveProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = false;
        task.sessions.pop();
        setWaves(updatedWaves);
    };
    return (
        <React.Fragment>
            <div>
                {waves.map((wave, waveProjectIndex) => (
                    <div key={`wave${waveProjectIndex}`} className='containerBox'>
                        <div className='containerBox'>
                            <div className='containerBox bold color-neogreen size30 lh-30'>
                                {wave.description}
                            </div>
                            <div className='containerBox'>   
                                <div className='size20 color-lite bold m-5'>
                                    {String(wave.createdDate).split(', ')[0]}
                                </div>
                                <div className='size20 color-lite m-5'>
                                    {String(wave.createdDate).split(', ')[1]}
                                </div>
                                <div className='color-lite size20 m-10'>
                                    {icons.track} Start {getTotalTime(getProjectTime(wave))}
                                </div>
                            </div>
                            <div className='containerBox flexContainer'>
                                <div 
                                    className='containerBox flex2Column m-10 bg-lite button' 
                                    onClick={() => addSet(waveProjectIndex)}
                                >
                                    + {icons.waveSet}
                                </div>
                                <div 
                                    className='containerBox flex2Column m-10 bg-lite button' 
                                    onClick={() => deleteProject(waveProjectIndex)}
                                >
                                    {icons.delete} Delete
                                </div>
                            </div>
                        </div>
                        <div>
                            {(isCollapsed || wave.isCollapsed) ? null : wave.tasks.map((task, taskIndex) => (
                                <div key={`task${taskIndex}`} className='containerBox lowerBorder'>
                                    <div className='containerBox bold'>
                                        <div className='color-yellow size25 lh-30'>
                                            {task.description}
                                        </div>
                                        <div>
                                            Waves: {task.sessions.length}
                                        </div>
                                        <div className='mt-5'>
                                            {
                                                task.sessions.map((wave, waveIndex) => <span key={`wave${waveIndex}`} className={`${(wave.isLiked)?'size30':''}`}>
                                                                                            {icons.wave}
                                                                                        </span>)
                                            }
                                        </div>
                                    </div>
                                    <div className='containerBox flexContainer'>
                                        <div 
                                            className='containerBox flex3Column bg-lite button' 
                                            onClick={() => addWave(waveProjectIndex, taskIndex)}
                                        >
                                            + {icons.wave} 
                                        </div>
                                        <div 
                                            className='containerBox flex3Column bg-lite button' 
                                            onClick={() => subtractWave(waveProjectIndex, taskIndex)}
                                        >
                                            - {icons.wave}
                                        </div>
                                        <div 
                                            className='containerBox flex3Column bg-lite button' 
                                            onClick={() => handleDeleteTask(waveProjectIndex, taskIndex)}
                                        >
                                            {icons.delete} Delete
                                        </div>
                                    </div>
                                    <div className='containerBox'>
                                        {task.sessions.map((session, sessionIndex) => (
                                            <div key={`session${sessionIndex}`} className='containerBox color-lite bg-lite'>
                                                <div 
                                                    className='button' 
                                                    onClick={() => likeWave(waveProjectIndex, taskIndex, sessionIndex)}
                                                >
                                                    <span className='mr-5 size15'>
                                                        <span className='bold'>
                                                            Wave {sessionIndex + 1}
                                                        </span>
                                                         - {String(session.startDate).split(', ')[1]}
                                                    </span>
                                                    <img 
                                                        src={(task.sessions[sessionIndex].isLiked) ? thumbsUp : thumbsDown} 
                                                        alt='like' 
                                                        className='shaka mb--10' 
                                                    />
                                                </div>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                ))}
            </div>
        </React.Fragment>
)}

export default TrackWaves