 import icons from '../utils/icons';
 
 const CollapseToggleButton = ({
    title,
    isCollapsed,
    setCollapse,
 }) => {
    const toggle = () => {
        setCollapse(!isCollapsed);
    }
    return <div className='button' onClick={() => toggle()}>
                {title}
                <div className={'rt-25 ml-5 size25 button contentRight bold mt--20'}>
                    {
                        (!isCollapsed)
                            ? <div>{icons.up}</div>
                            : <div>{icons.down}</div>
                    }
                </div>
            </div>
 }
export default CollapseToggleButton