import React, { useState, useEffect } from 'react';
import getKey from '../utils/KeyGenerator';
import TimerDisplay from '../utils/TimerDisplay';
import {currentTime, currentDate} from '../utils/CurrentCalendar';
import initSession from './initSession';
import initTasks from './initTasks';
import initTask from './initTask';
import icons from './icons';
import getTotalTime from './getTotalTime';

const TrackTasks = ({
    tracking,
    tasks,
    setTasks,
    getProjectTime,
    deleteProject,
    toggleCollapseSubmenu,
    isCollapsed,
    toggleParentTimer
}) => {

    useEffect(() => {
        if (tracking === 'projects') {
            localStorage.setItem('projects', JSON.stringify(tasks));
        } else {
            localStorage.setItem('taskTracking', JSON.stringify(tasks));   
        }
        
    }, [tasks]);

    useEffect(() => {
        //tasks.map((task, index) => console.log(`TrackTasks => task.description(${index}): ${task.description}`) )
       /*
        const storedTasks = (tracking === 'projects') ? localStorage.getItem('projects') : localStorage.getItem('taskTracking');
        if (storedTasks) {
            setTasks(JSON.parse(storedTasks));
        } else {
            setTasks([initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0)]);
        }
        */
    }, []);

    const addTask = (index) => {
        const taskDescription = prompt('Enter task description:');
        if (taskDescription) {
            const sessions = [];
            sessions.push(initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0));
            const task = initTask(taskDescription, sessions);
            const updateTasks = [...tasks];
            updateTasks[index].tasks.unshift(task);
            updateTasks[index].isCollapsed = false;
            setTasks(updateTasks);
        }
    };
    const handleStartTask = (taskProjectIndex, taskIndex) => {
        const updateTasks = [...tasks];
        const task = updateTasks[taskProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = true;
        //task.startDate = currentDate();
        task.sessions.push(initSession(currentDate(), currentTime(), currentDate(), currentTime(), 0))
        setTasks(updateTasks);
        startTimer(taskProjectIndex, taskIndex);
    };
    const handleStopTask = (taskProjectIndex, taskIndex) => {
        const updateTasks = [...tasks];
        const task = updateTasks[taskProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = false;
        const startTime = task.sessions[task.sessions.length - 1].startTime;
        const startDate = task.sessions[task.sessions.length - 1].startDate;
        const endTime = currentTime();
        const endDate = currentDate();
        
        let runningTime = task.sessions[task.sessions.length - 1].runningTime;
        runningTime = (runningTime === undefined) 
            ? task.sessions[task.sessions.length - 2].runningTime 
            : runningTime;
        //alert(`runningTime: ${runningTime}`)
        const session = initSession(
                            startDate, 
                            startTime, 
                            endDate, 
                            endTime, 
                            runningTime
                        )
        //alert(JSON.stringify(session,null,2))
        task.runningTime = runningTime;
        task.runningTimeDisplay = getTotalTime(runningTime);
        task.sessions.push(session)
        setTasks(updateTasks);
        stopTimer(taskProjectIndex, taskIndex);
    };
    const startTimer = (taskProjectIndex, taskIndex) => {
        const updateTasks = [...tasks];
        const task = updateTasks[taskProjectIndex].tasks[taskIndex];

        task.timerId = setInterval(() => {
            setTasks(updateTasks);
        }, 1000);
    };

    const stopTimer = (taskProjectIndex, taskIndex) => {
        const updateTasks = [...tasks];
        const task = updateTasks[taskProjectIndex].tasks[taskIndex];
        const session = task.sessions[task.sessions.length - 1]
        clearInterval(task.timerId);
        task.sessions.splice((task.sessions.length - 2), 1);
        task.totalTime = task.totalTime + session.runningTime;
        setTasks(updateTasks);
    };

    const handleStartSubTask = (taskProjectIndex, taskIndex) => {
        console.log(`handleStartSubTask => taskProjectIndex: ${taskProjectIndex}, taskIndex: ${taskIndex}`);
        const updatedTasks = [...tasks];
        const task = updatedTasks[taskProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = true;
        const subtask = {
            startDate: currentDate(),
            startTime: currentTime(),
            endTime: '',
            endDate: ''
        };
        const session = {
            startDate: currentDate(),
            startTime: currentTime(),
            subTasks: [subtask],
            endTime: ''
        }
        task.sessions.push(subtask)
        updatedTasks[taskProjectIndex].tasks[taskIndex].sessions.push(subtask)
        setTasks(updatedTasks);
        startTimer(taskProjectIndex, taskIndex);
    };
    const handleStopSubTask = (taskProjectIndex, taskIndex) => {
        const updatedTasks = [...tasks];
        const task = updatedTasks[taskProjectIndex].tasks[taskIndex];
        task.sessions = task.sessions ?? [];
        task.isRunning = false;
        const startTime = task.sessions[task.sessions.length - 1].startTime;
        const startDate = task.sessions[task.sessions.length - 1].startDate;
        const endTime = currentTime(); 
        const endDate = currentDate();
        const totalTimeMilliseconds = Math.round(endTime - startTime);
        const totalTime = getTotalTime(totalTimeMilliseconds);
        let runningTime = task.sessions[task.sessions.length - 1].runningTime;
        runningTime = (runningTime === undefined) 
            ? task.sessions[task.sessions.length - 2].runningTime 
            : runningTime;
        //alert(`runningTime: ${runningTime}`)
        runningTime = (runningTime === '0s') 
            ? totalTimeMilliseconds 
            : (runningTime + totalTimeMilliseconds);
        const runningTimeDisplay = getTotalTime(runningTime);
        const session = {
            startDate: startDate,
            startTime: startTime,
            subTasks: [{
                startDate: startDate,
                startTime: startTime,
                endDate: endDate,
                endTime: endTime,
            }],
            endDate: endDate,
            endTime: endTime,
            totalTime: totalTime,
            runningTime: runningTime,
            runningTimeDisplay: runningTimeDisplay
        }
        //alert(JSON.stringify(session,null,2))
        task.runningTime = runningTime;
        task.runningTimeDisplay = runningTimeDisplay;
        task.sessions.push(session)
        setTasks(updatedTasks);
        stopTimer(taskProjectIndex, taskIndex);
    };
    const handleDeleteTask = (taskProjectIndex, taskIndex) => {
        const updatedTasks = [...tasks];
        updatedTasks[taskProjectIndex].tasks.splice(taskIndex, 1);
        setTasks(updatedTasks);
    };

    return <div> 
        {tasks.map((task, index) => (
            <div key={getKey(`${task.description}${index}`)} className='containerBox'>
                <div className='containerBox'>
                    <div className='containerBox bold color-neogreen m-10 size30 lh-30'>
                        {task.description}
                    </div>
                    <div className='containerBox contentCentered'>
                        <div className='containerBox color-lite size25 bold m-5 contentCentered'>
                            {String(task.createdDate).split(', ')[0]} - {String(task.createdDate).split(', ')[1].split(':')[0]}:{String(task.createdDate).split(', ')[1].split(':')[1]}
                        </div>
                        <div className=' color-lite size20 m-10 contentCentered'>
                            {icons.track} Start {getTotalTime(getProjectTime(task))}
                        </div>
                    </div>
                    <div className='containerBox flexContainer'>
                        <div 
                            className='containerBox flex3Column m-10 bg-lite button' 
                            onClick={() => addTask(index)}
                        >
                            + {icons.track} Start
                        </div>
                        <div 
                            className='containerBox flex3Column m-10 bg-lite button' 
                            onClick={() => deleteProject(index)}
                        >
                            {icons.delete} Delete
                        </div>
                        <div 
                            className='containerBox flex3Column m-10 bg-lite button' 
                            onClick={() => toggleCollapseSubmenu(index)}
                        >
                            {task.isCollapsed ? 'Expand' : 'Collapse'}
                        </div>
                    </div>
                </div>
                <div className='color-lite size15 bold containerBox'>
                    {(isCollapsed ?? task.isCollapsed) 
                        ? '' 
                        : `${task.description} Tasks:`}
                </div>
                <div>
                    {(isCollapsed ?? task.isCollapsed) 
                        ? null 
                        : task.tasks.map((task, taskIndex) => (
                            <div key={getKey(`${task.description}${taskIndex}`)} className='containerBox lowerBorder'>
                                <div className='containerBox'>
                                    <div className='bold color-yellow size25 lh-30'>
                                        {task.description}
                                    </div>
                                    <div>
                                        {task.sessions.length} Total: {task.runningTimeDisplay}
                                    </div>
                                </div>
                                {task.sessions.map((session, sessionIndex) => (
                                    <div className='color-lite containerBox' key={getKey(`${task.description}${sessionIndex}`)}>
                                        <div>
                                            <div key={getKey(`${task.description}${sessionIndex}${String(session.startDate).split(', ')[0]}`)}>
                                                <div className='containerBox flexContainer'>
                                                    <div className='flex1Auto'>
                                                        {String(session.startDate).split(', ')[0]}
                                                    </div>
                                                    <div className='flex3Column'>
                                                        {String(session.startDate).split(', ')[1]}
                                                    </div>
                                                    <div className='columnLeftAlign flex3Column'>
                                                        {String(session.endDate).split(', ')[1]} 
                                                    </div>
                                                </div>
                                                <div className='containerBox flexContainer color-neogreen'>
                                                    <div className='flex1Auto'>
                                                    </div>
                                                    <div className='flex3Column'>
                                                        {String(session.subTasks[0].startDate).split(', ')[1]}
                                                    </div>
                                                    <div className='columnLeftAlign flex3Column'>
                                                        {String(session.subTasks[0].endDate).split(', ')[1]}
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className='width-100-percent'>
                                            <TimerDisplay 
                                                totalTime={session.totalTime} 
                                                toggleParentTimer={toggleParentTimer} 
                                                index={index} 
                                                taskIndex={taskIndex} 
                                                handleStopSubTask={handleStopSubTask}
                                                handleStartSubTask={handleStartSubTask}
                                            />
                                        </div>
                                    </div>
                                    ))}
                                <div className='containerBox flexContainer'>
                                    {task.isRunning 
                                        ? <div 
                                            className='containerBox flex2Column bg-lite button m-5' 
                                            onClick={() => handleStopTask(index, taskIndex)}
                                            >
                                                {icons.stop} Stop 
                                            </div>
                                        : <div 
                                            className='containerBox flex2Column bg-lite button m-5' 
                                            onClick={() => handleStartTask(index, taskIndex)}
                                            >
                                                {icons.track} Start
                                            </div>
                                    }
                                        <div 
                                            className='containerBox flex2Column bg-lite button m-5' 
                                            onClick={() => handleDeleteTask(index, taskIndex)}
                                        >
                                            {icons.delete} Delete
                                        </div>
                                </div>
                            </div>
                        ))}
                </div>
            </div>
        ))}
    </div>
}

export default TrackTasks;